import {useEffect, useState} from "react";
import axios from "axios";
import {Button, Container, Form} from "react-bootstrap";
import examService from "../../services/exam";
import locationList from '../../utils/locationList.json'
import {enqueueSnackbar} from "notistack";

const ExamModify = () => {
    // 
    const [exams, setExams] = useState([]); // 
    const [exam, setExam] = useState({}); // 
    const [name, setName] = useState(""); // 
    const [startTime, setStartTime] = useState(""); // 
    const [endTime, setEndTime] = useState(""); // 
    const [location, setLocation] = useState(0); // 
    const [isLoading, setIsLoading] = useState(false); // 
    const [examId, setExamId] = useState(null)
    // 
    useEffect(() => {
        examService.getAllForAdmin()
            .then(result => {
                setExams(result)
            })
    }, []);

    // 
    const handleSelectChange = (e) => {
        const id = parseInt(e.target.value);
        setExamId(id)
        const selectedExam = exams.find((exam) => exam.id === id);

        setExam(selectedExam);
        setName(selectedExam.name);
        setStartTime(selectedExam.startTime);
        setEndTime(selectedExam.endTime);
        setLocation(selectedExam.location);
    };

    // 
    const handleSubmit = async (e) => {
        e.preventDefault(); // 

        setIsLoading(true);

        const editedExam = {
            name: name,
            startTime: startTime,
            endTime: endTime,
            location: location,
            id: examId
        };

        const result = await examService.modifyExam(editedExam);
        enqueueSnackbar('')
        setIsLoading(false);
    };

    return (
        <Container>
            <h1></h1>

            <Form onSubmit={handleSubmit}>
                {/* eslint-disable-next-line react/jsx-no-undef */}
                <Form.Group controlId="formSelectExam">
                    <Form.Label></Form.Label>
                    <Form.Control as="select" onChange={handleSelectChange}>
                        <option value=""></option>
                        {exams.map((exam) => (
                            <option value={exam.id} key={exam.id}>
                                {exam.name}
                            </option>
                        ))}
                    </Form.Control>
                </Form.Group>

                {exam.id && (
                    <>
                        <Form.Group controlId="formExamName">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group controlId="formStartTime">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="datetime-local"
                                value={startTime}
                                onChange={(e) => setStartTime(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group controlId="formEndTime">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="datetime-local"
                                value={endTime}
                                onChange={(e) => setEndTime(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group controlId="formLocation">
                            <Form.Label></Form.Label>
                            <Form.Control
                                as="select"
                                value={location}
                                onChange={(e) => setLocation(parseInt(e.target.value))}
                            >
                                {locationList.map((location) =>
                                    (
                                        <option value={location.value}>{location.name}</option>
                                    )
                                )}
                            </Form.Control>
                        </Form.Group>

                        <Button variant="primary" type="submit" disabled={isLoading}>
                            {isLoading ? "..." : ""}
                        </Button>
                    </>
                )}
            </Form>
        </Container>
    );
};
export default ExamModify
