import moment from "moment";
export const intToWeekday=(n)=>{
    const weekdays=["","","","","","",""]
    return weekdays[n-1]
}
// moment

// 
// 
moment.locale('zh-cn', {
    months: '___________'.split('_'),
    monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split('_'),
    weekdays: '______'.split('_'),
    weekdaysShort: '______'.split('_'),
    weekdaysMin: '______'.split('_'),
    longDateFormat: {
        LT: 'HH:mm',
        LTS: 'HH:mm:ss',
        L: 'YYYY-MM-DD',
        LL: 'YYYYMMDD',
        LLL: 'YYYYMMDDAhmm',
        LLLL: 'YYYYMMDDddddAhmm',
        l: 'YYYY-M-D',
        ll: 'YYYYMD',
        lll: 'YYYYMD HH:mm',
        llll: 'YYYYMDdddd HH:mm'
    },
    meridiemParse: /|||||/,
    meridiemHour: function (hour, meridiem) {
        if (hour === 12) {
            hour = 0;
        }
        if (meridiem === '' || meridiem === '' ||
            meridiem === '') {
            return hour;
        } else if (meridiem === '' || meridiem === '') {
            return hour + 12;
        } else {
            // ''
            return hour >= 11 ? hour : hour + 12;
        }
    },
    meridiem: function (hour, minute, isLower) {
        const hm = hour * 100 + minute;
        if (hm < 600) {
            return '';
        } else if (hm < 900) {
            return '';
        } else if (hm < 1130) {
            return '';
        } else if (hm < 1230) {
            return '';
        } else if (hm < 1800) {
            return '';
        } else {
            return '';
        }
    },
    calendar: {
        sameDay: '[]LT',
        nextDay: '[]LT',
        nextWeek: '[]ddddLT',
        lastDay: '[]LT',
        lastWeek: '[]ddddLT',
        sameElse: 'L'
    },
    dayOfMonthOrdinalParse: /\d{1,2}(||)/,
    ordinal: function (number, period) {
        switch (period) {
            case 'd':
            case 'D':
            case 'DDD':
                return number + '';
            case 'M':
                return number + '';
            case 'w':
            case 'W':
                return number + '';
            default:
                return number;
        }
    },
    relativeTime: {
        future: '%s',
        past: '%s',
        s: '',
        ss: '%d',
        m: '1',
        mm: '%d',
        h: '1',
        hh: '%d',
        d: '1',
        dd: '%d',
        M: '1',
        MM: '%d',
        y: '1',
        yy: '%d'
    },
    week: {
        // GB/T 7408-1994ISO 8601:1988
        dow: 1, // Monday is the first day of the week.
        doy: 4  // The week that contains Jan 4th is the first week of the year.
    }
})
export const dateFormatter=(date)=>{
    return moment(date).format('lll')
}
export const timeFormatter=(date)=>{
    return moment(date).format('YYYY/MM/DD, HH:mm:ss')
}
export const withInAnHour=(baseHour,baseMinute,targetHour)=>{
    const interval=((targetHour*60)-(baseHour*60+baseMinute));
    return interval<=60 && interval>0
}
export const inAnHour=(baseTime,targetTime)=>{
    const interval=targetTime-baseTime
    return ((interval<=3600*1000)&& (interval>0) )
}

export const range = (start, end, step = 1) => {
    let output = [];
    if (typeof end === 'undefined') {
        end = start;
        start = 0;
    }
    for (let i = start; i < end; i += step) {
        output.push(i);
    }
    return output;
};
