import { useEffect, useState } from "react";
import { Button, Container, Form } from "react-bootstrap";
import courseService from "../../services/course";
import locationList from '../../utils/locationList.json';
import { enqueueSnackbar } from "notistack";

const CourseModify = () => {
    // 
    const [courses, setCourses] = useState([]); // 
    const [course, setCourse] = useState({}); // 
    const [name, setName] = useState(""); // 
    const [weekday, setWeekday] = useState(1); // 
    const [startTime, setStartTime] = useState(8); // 
    const [duration, setDuration] = useState(2); // 
    const [periodic, setPeriodic] = useState(false); // 
    const [location, setLocation] = useState(0); // 
    const [isLoading, setIsLoading] = useState(false); // 
    const [courseId,setCourseId]=useState(null)
    // 
    useEffect(() => {
        courseService.getAllForAdmin()
            .then(result => {
                setCourses(result)
            })
    }, []);

    // 
    const handleSelectChange = (e) => {
        const id = parseInt(e.target.value);
        setCourseId(id)
        const selectedCourse = courses.find((course) => course.id === id);

        setCourse(selectedCourse);
        setName(selectedCourse.name);
        setWeekday(selectedCourse.weekday);
        setStartTime(selectedCourse.startTime);
        setDuration(selectedCourse.duration);
        setPeriodic(selectedCourse.periodic);
        setLocation(selectedCourse.location);
    };

    // 
    const handleSubmit = async (e) => {
        e.preventDefault(); // 

        setIsLoading(true);

        const editedCourse = {
            time:startTime,
            location:location,
            weekday:weekday,
            duration:duration
        };

        const result = await courseService.modifyCourse(editedCourse,courseId);

        setIsLoading(false);
        if (result) {
            enqueueSnackbar('');
        } else {
            enqueueSnackbar('');
        }
    };

    return (
        <Container>
            <h1></h1>

            <Form onSubmit={handleSubmit}>
                {/*  */}
                <Form.Group controlId="formSelectCourse">
                    <Form.Label></Form.Label>
                    <Form.Control as="select" onChange={handleSelectChange}>
                        <option value=""></option>
                        {courses.map((course) => (
                            <option value={course.id} key={course.id}>
                                {course.name}
                            </option>
                        ))}
                    </Form.Control>
                </Form.Group>

                {course.id && (
                    <>
                        {/*  */}
                        {/*<Form.Group controlId="formCourseName">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                            />
                        </Form.Group>*/}

                        {/*  */}
                        <Form.Group controlId="formWeekday">
                            <Form.Label></Form.Label>
                            <Form.Control
                                as="select"
                                value={weekday}
                                onChange={(e) => setWeekday(parseInt(e.target.value))}
                            >
                                <option value={1}></option>
                                <option value={2}></option>
                                <option value={3}></option>
                                <option value={4}></option>
                                <option value={5}></option>
                                <option value={6}></option>
                                <option value={7}></option>
                            </Form.Control>
                        </Form.Group>

                        {/*  */}
                        <Form.Group controlId="formStartTime">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="number"
                                min={0}
                                max={23}
                                placeholder=""
                                value={startTime}
                                onChange={(e) => setStartTime(parseInt(e.target.value))}
                            />
                        </Form.Group>

                        {/*  */}
                        <Form.Group controlId="formDuration">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="number"
                                min={1}
                                max={8}
                                placeholder=""
                                value={duration}
                                onChange={(e) => setDuration(parseInt(e.target.value))}
                            />
                        </Form.Group>

                        {/*  */}
                        <Form.Group controlId="formPeriodic">
                            <Form.Check
                                type="checkbox"
                                label=""
                                checked={periodic}
                                onChange={(e) => setPeriodic(e.target.checked)}
                            />
                        </Form.Group>

                        {/*  */}
                        <Form.Group controlId="formLocation">
                            <Form.Label></Form.Label>
                            <Form.Control
                                as="select"
                                value={location}
                                onChange={(e) => setLocation(parseInt(e.target.value))}
                            >
                                {locationList.map((location) => (
                                    <option value={location.value} key={location.value}>
                                        {location.name}
                                    </option>
                                ))}
                            </Form.Control>
                        </Form.Group>

                        {/*  */}
                        <Button variant="primary" type="submit" disabled={isLoading}>
                            {isLoading ? "..." : ""}
                        </Button>
                    </>
                )}
            </Form>
        </Container>
    );
};

export default CourseModify;
