import React from 'react';
import {Link, useNavigate} from 'react-router-dom';
import '../sidebar.css'
import {Button} from "react-bootstrap";
import {useDispatch, useSelector} from "react-redux";
import {clearUser} from "../slice/userSlice";
import {resetCookie} from "../utils/cookie";
import TimeSwitch from "./TimeSwitch";
import NavigationModal from "./NavigationModal";

function Sidebar() {
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const userGroup = useSelector(state => state.user.userGroup)
    const logoutHandler = (e) => {
        e.preventDefault()
        resetCookie()
        navigate('/')
        dispatch(clearUser())
    }
    const studentNav = [
        {to: "/", name: ""},
        {to: "/courses", name: ""},
        {to: "/exams", name: ""},
        {to: "/events", name: ""},
        {to: "/addEvent", name: ""},
        {to:'/activity',name:''},
        {to:'/addActivity',name:''}
    ];
    const adminNav = [
        {to: '/', name: ''},
        {to: '/releaseCourse', name: ''},
        {to: '/modifyCourse', name: ''},
        {to: '/releaseExam', name: ''},
        {to: '/modifyExam', name: ''},
        {to:'/addActivity',name:''},
    ]
    const finalNav = (userGroup === 'admin'
        ? adminNav
        : studentNav)
    return (
        <div className='sidebar'>
            <div className="sidebar-header">
                <h3></h3>
            </div>
            <ul className="list-unstyled components">
                {
                    finalNav.map((nav,index) => {
                        return (
                            <li key={index}>
                                <Link to={nav.to} className='nav-link'>
                                    {nav.name}
                                </Link>
                            </li>
                        )
                    })
                }
                <li>
                    <Link to='/' className='nav-link' onClick={logoutHandler}>
                        
                    </Link>
                </li>
                <li>
                    <TimeSwitch/>
                </li>
                <li>
                    <NavigationModal/>
                </li>
            </ul>
        </div>
    );
}

export default Sidebar;

